<template>
  <div class="register">
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
        autocomplete
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
        autocomplete
      />
      <van-field
        v-model="password2"
        type="password"
        name="password2"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
        autocomplete
      />
      <div style="margin: 20px;">
        <van-button round block type="danger" native-type="submit" class="btn">注册</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      username: "",
      password: "",
      password2: "",
    };
  },
  methods: {
    onSubmit(params) {
      let _this = this;
      this.$axios.get("register", { params }).then(res => {
        console.log(res);
        // 请求数据成功后提示
        if (res.data == "200") {
          Toast({
            message: "注册成功",
            onOpened() {
              // 回调跳转路由
              _this.$router.push("/login/mylogin");
            }
          });
        } else {
          Toast("注册失败");
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.register {
  height: 200px;
  .btn {
    margin-top: 30px;
    margin-bottom: 60px;
  }
}
</style>